
import { useState,useLayoutEffect } from "react";
import mapboxgl from "mapbox-gl/dist/mapbox-gl.js";
import "./StoreLocator.css"

 
 export default function StoreLocator(){

     // TODO: replace with your access token
    const [marker, setMarker]=useState()
    mapboxgl.accessToken = "pk.eyJ1Ijoia2Vsa2lwMjAwMSIsImEiOiJja3NmMm85NjIxMzVpMnFvNGhhbnE0NnM1In0.2jXs74yu5DAAyXaapfpSJA";

    useLayoutEffect(() => {
        const map = new mapboxgl.Map({
            container: 'map',
            style: 'mapbox://styles/mapbox/streets-v11',
            center:  [ 12.567898, 55.67583 ],
            zoom: 7
        });
        const marker = new mapboxgl.Marker()
        marker.setLngLat([12.567898, 55.67583])

        
        //console.log(stores['central'])
        marker.addTo(map);
        setMarker(marker);
    }, []);

    const stores = {
        central: [12.567898, 55.67583],
        norrebro: [12.553806, 55.699299],
        airport: [12.650784, 55.618042]
    }
    
    

const handleSelectChange =(e)=>{
    const location = e.target.value
    console.log(marker)
    marker.setLngLat(stores[location])
   
}

    return <>
        <div className="map-overlay">
            <h3>Choose store: </h3>
            <select onChange={handleSelectChange}>
                <option value="central">Central station</option>
                <option value="norrebro">Norrebro street</option>
                <option value="airport">CPH Airport</option>
            </select>
        </div>
        <div id="map" className="map"></div>
    </>;
}
