import React, {useState} from "react";
import AddProductForm from "./AddProductForm.js"
import ProductsList from "./ProductsList.js"

export default function StoreFront() {
    const [products, setProducts] = useState([]);
    const [name, setName] = useState("");
    const [description, setDescription] = useState("");
    const [validation, setValidation] = useState("");

    function handleFormSubmit(event) {
        event.preventDefault();

        if (!name) {
            setValidation("Please enter a name");
            return ;
        }
        if (!description){
            setValidation("Please enter a description");
            return ;
        }
        setProducts([...products, {
            id: products.length + 1,
            name,
            description,
        }]);
        setName("");
        setDescription("");
        setValidation("");
    }
const  handleDeleteClick=(id)=>{
    setProducts(products.filter(product => product.id !== id));}

const handleNameChange=(e)=>{
    setName(e.target.value) }

const handleDescriptionChange = (e)=>{
    setDescription(e.target.value)}

    return (<>
<AddProductForm name={name} description={description} validation={validation} products={products}onFormSubmit={handleFormSubmit} onNameChange={handleNameChange} onDescriptionChange={handleDescriptionChange}/>


<ProductsList products={products} onsetProduct={setProducts} onDeleteClick={handleDeleteClick}/>
    </>);
}