import React,{useEffect,useState} from "react";
import "./clock.css"

export default function Clock() {
    const [date,setDate]= useState(new Date())
    

    useEffect(()=>{
        const id = setInterval(()=>{
            setDate(new Date())
            
        },1000)
    return()=>{
clearInterval(id)
    }},[])
 
    return <h2>{date.toLocaleTimeString()}</h2>;
}
