import React,{useState} from "react"

export default function Statem() {

    const [history, setHistory]= useState([]);

    return (<>
    <label htmlFor="01">Names:</label>
    <input placeholder="Enter Name" id="01" onChange={(e)=>{
        setHistory([e.target.value])
    }}/> 
    <hr/>
    <br/>
    <ol>
    {history.map(h=>{
        return <li>{h}</li>
    })}
    </ol>
  
    <hr/>
    </>)
}
    
